import PropTypes from "prop-types";
import { CodeXml, Maximize, Minimize } from "lucide-react";

const ModernStatusBar = ({
  wordCount,
  charCount,
  onToggleSource,
  isFullScreen,
  onToggleFullScreen,
}) => (
  <>
  
  <style>
    {`
    /* MODERN STATUS BAR STYLES */

.modern-statusbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 16px;
  font-size: 13px;
  transition: background-color 0.3s ease, border-color 0.3s ease,
    color 0.3s ease;
  border-top: 1px solid;
}

.statusbar-segment {
  display: flex;
  align-items: center;
  gap: 12px;
}

.statusbar-divider {
  opacity: 0.5;
}

.statusbar-button {
  background: none;
  border: none;
  border-radius: 6px;
  padding: 6px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease, color 0.2s ease;
}

/* --- Light Theme --- */
.theme-light .modern-statusbar {
  background-color: #f8f9fa;
  border-color: #e0e0e0;
  color: #5f6368;
}

.theme-light .statusbar-button {
  color: #5f6368;
}

.theme-light .statusbar-button:hover {
  background-color: #f1f3f4;
  color: #202124;
}

/* --- Dark Theme --- */
.theme-dark .modern-statusbar {
  background-color: #2c2d2f;
  border-color: #3c4043;
  color: #bdc1c6;
}

.theme-dark .statusbar-button {
  color: #bdc1c6;
}

.theme-dark .statusbar-button:hover {
  background-color: #3c4043;
  color: #e8eaed;
}

    `
    }
  </style>
  

  <div className="modern-statusbar">
    <div className="statusbar-segment">
      <span className="statusbar-item">Words: {wordCount}</span>
      <span className="statusbar-divider">|</span>
      <span className="statusbar-item">Characters: {charCount}</span>
    </div>
    <div className="statusbar-segment">
      <button
        className="statusbar-button"
        title="View HTML Source"
        onClick={onToggleSource}
      >
        <CodeXml size={18} />
      </button>
      <button
        className="statusbar-button"
        title={isFullScreen ? "Exit Full Screen" : "Full Screen"}
        onClick={onToggleFullScreen}
      >
        {isFullScreen ? <Minimize size={18} /> : <Maximize size={18} />}
      </button>
    </div>
  </div>
    </>
);

ModernStatusBar.propTypes = {
  wordCount: PropTypes.number.isRequired,
  charCount: PropTypes.number.isRequired,
  onToggleSource: PropTypes.func.isRequired,
  onToggleFullScreen: PropTypes.func.isRequired,
  isFullScreen: PropTypes.bool.isRequired,
};

export default ModernStatusBar;
