import { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import { ChevronDown } from "lucide-react";

const FuturisticDropdown = ({ title, icon, children }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <div className="futuristic-dropdown" ref={dropdownRef}>
      <button
        type="button"
        title={title}
        onClick={() => setIsOpen(!isOpen)}
        onMouseDown={(e) => e.preventDefault()}
        className="futuristic-button"
        aria-expanded={isOpen}
      >
        <span className="button-content">
          {icon}
          <ChevronDown
            size={16}
            className={`chevron-icon ${isOpen ? "open" : ""}`}
          />
        </span>
      </button>
      <div
        className={`futuristic-dropdown-menu ${isOpen ? "show" : ""}`}
        onClick={() => setIsOpen(false)}
      >
        {children}
      </div>
    </div>
  );
};

FuturisticDropdown.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
};

export default FuturisticDropdown;
