import { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";

/**
 * A custom, self-contained Dropdown component.
 */
const Dropdown = ({ title, icon, children }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <div className="btn-group" ref={dropdownRef}>
      <button
        type="button"
        title={title}
        onClick={() => setIsOpen(!isOpen)}
        onMouseDown={(e) => e.preventDefault()}
        className="btn btn-outline-secondary dropdown-toggle d-flex align-items-center gap-1"
        aria-expanded={isOpen}
      >
        {icon}
      </button>
      <div
        className={`dropdown-menu ${isOpen ? "show" : ""}`}
        onClick={(e) => {
          // Close dropdown if a button inside it is clicked
          if (e.target.closest("button")) {
            setIsOpen(false);
          }
        }}
      >
        {children}
      </div>
    </div>
  );
};

Dropdown.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
};

export default Dropdown;
