import React from "react";
import PropTypes from "prop-types";
import { Sparkles, Minimize, ChevronsDownUp } from "lucide-react";

/**
 * A loading popup for the AI generation process.
 */
const AiLoadingPopup = ({ status, isMinimized, onMinimizeToggle }) => {
  if (isMinimized) {
    return (
      <div className="ai-loading-popup-minimized">
        <div className="spinner-border spinner-border-sm text-primary" role="status"></div>
        <span className="ms-2 small">AI is working...</span>
        <button onClick={onMinimizeToggle} className="btn btn-sm btn-light ms-auto">
          <ChevronsDownUp size={14} />
        </button>
      </div>
    );
  }

  return (
    <div className="ai-loading-popup">
      <div className="d-flex justify-content-between align-items-center mb-3">
        <h6 className="mb-0 d-flex align-items-center gap-2">
          <Sparkles size={18} className="text-primary" />
          AI Assistant
        </h6>
        <button onClick={onMinimizeToggle} className="btn btn-sm btn-light">
          <Minimize size={14} />
        </button>
      </div>
      <div className="d-flex align-items-center">
        <div className="spinner-border text-primary me-3" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <div>
          <p className="mb-0 fw-bold">Working on it...</p>
          <p className="mb-0 text-muted small">{status}</p>
        </div>
      </div>
    </div>
  );
};

AiLoadingPopup.propTypes = {
  status: PropTypes.string.isRequired,
  isMinimized: PropTypes.bool.isRequired,
  onMinimizeToggle: PropTypes.func.isRequired
};

export default AiLoadingPopup;
