import PropTypes from "prop-types";
import ClassicToolbar from "./theme/classic/ClassicToolbar";
import ModernToolbar from "./theme/modern/ModernToolbar";
import FuturisticToolbar from "./theme/futuristic/FuturisticToolbar";

const Toolbar = ({ theme, ...props }) => {
  if (theme === "modern") {
    return <ModernToolbar {...props} />;
  }
  if (theme === "futuristic") {
    return <FuturisticToolbar {...props} />;
  }
  return <ClassicToolbar {...props} />;
};

Toolbar.propTypes = {
  theme: PropTypes.oneOf(["classic", "modern", "futuristic"]),
};
export default Toolbar;
