export function injectBootstrap() {
  if (typeof document !== "undefined") {
    const cssLinkId = "bootstrap-css-cdn-link";
    const jsScriptId = "bootstrap-js-cdn-script";

    // --- Inject Bootstrap CSS ---
    if (!document.getElementById(cssLinkId)) {
      const link = document.createElement("link");
      link.id = cssLinkId;
      link.rel = "stylesheet";
      link.href = "https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css";
      link.crossOrigin = "anonymous";
      document.head.appendChild(link);
    }

    // We check if the script is already there to avoid adding it multiple times.
    if (!document.getElementById(jsScriptId)) {
      const script = document.createElement("script");
      script.id = jsScriptId;
      script.src = "https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/js/bootstrap.bundle.min.js";
      script.crossOrigin = "anonymous";
      script.defer = true; 
      document.body.appendChild(script);
    }
  }
}