import React from "react";
import PropTypes from "prop-types";
import { Sparkles } from "lucide-react";
import BaseModal from "../modal/BaseModal";

/**
 * Modal to display the AI-generated content suggestion with a diff view.
 */
const AiSuggestionModal = ({ isOpen, onClose, onApply, originalContent, suggestion, highlightedDiff }) => {
  if (!isOpen) return null;

  return (
    <BaseModal onClose={onClose} title="AI Content Suggestion" icon={<Sparkles size={20} className="text-primary" />} size="modal-xl">
      <div className="modal-body p-4">
        <ul className="nav nav-tabs mb-3" role="tablist">
          <li className="nav-item" role="presentation">
            <button
              className="nav-link active"
              id="preview-tab"
              data-bs-toggle="tab"
              data-bs-target="#preview-pane"
              type="button"
              role="tab"
              aria-controls="preview-pane"
              aria-selected="true">
              Preview Changes
            </button>
          </li>
          <li className="nav-item" role="presentation">
            <button
              className="nav-link"
              id="diff-tab"
              data-bs-toggle="tab"
              data-bs-target="#diff-pane"
              type="button"
              role="tab"
              aria-controls="diff-pane"
              aria-selected="false">
              Highlight Changes
            </button>
          </li>
        </ul>
        <div className="tab-content">
          <div className="tab-pane fade show active" id="preview-pane" role="tabpanel" aria-labelledby="preview-tab">
            <div className="row">
              <div className="col-md-6">
                <h6 className="mb-2 text-muted">Original Content</h6>
                <div className="ai-preview-pane" dangerouslySetInnerHTML={{ __html: originalContent }} />
              </div>
              <div className="col-md-6">
                <h6 className="mb-2 text-success">AI Suggestion</h6>
                <div className="ai-preview-pane" dangerouslySetInnerHTML={{ __html: suggestion }} />
              </div>
            </div>
          </div>
          <div className="tab-pane fade" id="diff-pane" role="tabpanel" aria-labelledby="diff-tab">
            <h6 className="mb-2 text-primary">Highlighted Differences</h6>
            <p className="small text-muted">
              <span className="px-2 py-1 me-2 rounded" style={{ backgroundColor: "#d4edda" }}>
                Added content
              </span>
              <span className="px-2 py-1 rounded" style={{ backgroundColor: "#f8d7da" }}>
                Removed content
              </span>
            </p>
            <div className="ai-preview-pane" dangerouslySetInnerHTML={{ __html: highlightedDiff }} />
          </div>
        </div>
      </div>
      <div className="modal-footer border-0 bg-light">
        <button type="button" className="btn editor-secondary" onClick={onClose}>
          Cancel
        </button>
        <button type="button" className="btn editor-primary" onClick={onApply}>
          Apply Suggestion
        </button>
      </div>
    </BaseModal>
  );
};

AiSuggestionModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onApply: PropTypes.func.isRequired,
  originalContent: PropTypes.string,
  suggestion: PropTypes.string,
  highlightedDiff: PropTypes.string
};

export default AiSuggestionModal;
