import PropTypes from "prop-types";

/**
 * A generic, reusable modal component.
 */
const BaseModal = ({ children, onClose, title, icon, size = "modal-dialog-centered" }) => (
  <div className="modal fade show" style={{ display: "block", backgroundColor: "rgba(0,0,0,0.5)" }} onClick={onClose}>
    <div className={`modal-dialog ${size}`} onClick={e => e.stopPropagation()}>
      <div className="modal-content border-0 shadow-lg">
        <div className="modal-header bg-light border-0">
          <h5 className="modal-title d-flex align-items-center gap-2">
            {icon}
            {title}
          </h5>
          <button type="button" className="btn-close" onClick={onClose}></button>
        </div>
        {children}
      </div>
    </div>
  </div>
);

BaseModal.propTypes = {
  children: PropTypes.node.isRequired,
  onClose: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  icon: PropTypes.node,
  size: PropTypes.string
};

export default BaseModal;
