import React from "react";
import PropTypes from "prop-types";

const ModernToolbarButton = React.memo(
  ({
    onClick,
    title,
    command,
    activeStyles,
    disabled = false,
    children,
    className = "",
  }) => (
    <button
      type="button"
      onClick={onClick}
      onMouseDown={(e) => e.preventDefault()}
      className={`modern-toolbar-button ${
        activeStyles && activeStyles.has(command) ? "active" : ""
      } ${className}`}
      title={title}
      disabled={disabled}
    >
      {children}
    </button>
  )
);

ModernToolbarButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  command: PropTypes.string,
  activeStyles: PropTypes.object,
  disabled: PropTypes.bool,
  children: PropTypes.node.isRequired,
  className: PropTypes.string,
};

ModernToolbarButton.displayName = "ModernToolbarButton";

export default ModernToolbarButton;
