import PropTypes from "prop-types";
import { Palette, Highlighter } from "lucide-react";
import FuturisticDropdown from "./FuturisticDropdown";

const COLORS = [
  "#FF00FF",
  "#00FFFF",
  "#FFFF00",
  "#FF007F",
  "#00FF7F",
  "#FFFFFF",
  "#C0C0C0",
  "#808080",
  "#404040",
  "#000000",
  "#FF0000",
  "#00FF00",
  "#0000FF",
  "#800080",
  "#FFA500",
];

const FuturisticColorPicker = ({ command, applyStyle }) => {
  const title = command === "foreColor" ? "Text Color" : "Highlight Color";
  const icon =
    command === "foreColor" ? <Palette size={18} /> : <Highlighter size={18} />;

  return (
    <FuturisticDropdown title={title} icon={icon}>
      <div className="futuristic-color-picker">
        {COLORS.map((color) => (
          <button
            key={color}
            type="button"
            className="color-swatch-hexagon"
            onClick={() => applyStyle(command, color)}
            aria-label={color}
          >
            <div className="hexagon-inner" style={{ backgroundColor: color }} />
          </button>
        ))}
      </div>
    </FuturisticDropdown>
  );
};

FuturisticColorPicker.propTypes = {
  command: PropTypes.oneOf(["foreColor", "backColor"]).isRequired,
  applyStyle: PropTypes.func.isRequired,
};

export default FuturisticColorPicker;
