import { useState } from "react";
import PropTypes from "prop-types";
import { Video, Link } from "lucide-react";
import BaseModal from "./BaseModal";

/**
 * Modal for inserting a video from a URL.
 */
const VideoModal = ({ isOpen, onClose, onInsert }) => {
  const [videoUrl, setVideoUrl] = useState("");

  if (!isOpen) return null;

  const handleInsert = () => {
    onInsert(videoUrl);
    setVideoUrl("");
  };

  return (
    <BaseModal
      onClose={onClose}
      title="Insert Video"
      icon={<Video size={20} className="text-primary" />}
    >
      <div className="modal-body p-4">
        <label className="form-label fw-semibold">Video URL</label>
        <div className="input-group">
          <span className="input-group-text">
            <Link size={16} />
          </span>
          <input
            type="url"
            className="form-control"
            value={videoUrl}
            onChange={(e) => setVideoUrl(e.target.value)}
            placeholder="e.g., https://www.youtube.com/watch?v=..."
            autoFocus
          />
        </div>
        <div className="form-text video-modal-title">
          Supports YouTube and Vimeo links.
        </div>
      </div>
      <div className="modal-footer border-0 bg-light">
        <button
          type="button"
          className="btn editor-secondary"
          onClick={onClose}
        >
          Cancel
        </button>
        <button
          type="button"
          className="btn editor-primary"
          onClick={handleInsert}
        >
          Insert Video
        </button>
      </div>
    </BaseModal>
  );
};

VideoModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onInsert: PropTypes.func.isRequired,
};

export default VideoModal;
